img {
    max-width: 100%;
    max-height: 100%;
}

.title h1{
  font-family: Lato, 'Courier Bold', sanserif;
  font-weight: bold;
  font-variant: small-caps;
  font-size: 60px;
  margin-left: 30px;
  color: "#335CD6";
  background-color: whitesmoke;
  float: left;
}
.container {
    position: relative;
    text-align: center;
    color: white;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  background: rgba(0, 0, 0, 0.6);
}



